<template>
  <div class="grid">
    <div class="header">
      <div class="left">
        <div class="search">
          <a href="/">
            <img id="g_logo" src="../assets/images/logo-small.png" srcset="../assets/images/logo-small@2x.png 2x, ../assets/images/logo-small@3x.png 3x" name="g_logo" />
          </a>
          <h1>{{ pageTitle }}</h1>
        </div>
      </div>
      <!--
      <div class="g_apps right">
        <img id="profile_pic" src="images/profile.png" name="profile_pic" />
      </div>
      //-->
    </div>

    <div class="s_results page">
      <component :is="page" :key="$route.fullPath" />
    </div>
    <Footer/>
  </div>
</template>

<script>
import Footer from '@/components/Footer'
import About from '@/components/Page/About'
import Disclaimer from '@/components/Page/Disclaimer'
import Settings from '@/components/Page/Settings'

export default {
  name: 'Page',
  props: {
    page: String
  },
  data () {
    return {
    }
  },
  components: {
    About,
    Disclaimer,
    Settings,
    Footer
  },
  mounted () {
  },
  computed: {
    pageTitle () {
      let resultString = this.page.replace(/([a-z])([A-Z])/g, '$1 $2')
      resultString = resultString.replace(/([A-Z])([A-Z][a-z])/g, '$1 $2')
      resultString = resultString.charAt(0).toUpperCase() + resultString.substr(1)
      return resultString
    }
  },
  methods: {
  }
}
</script>

<style>
h1{
  color: #5f6368;
  display: block;
  font-family: 'Google Sans',sans-serif;
  font-size: 20px;
  font-weight: 400;
  margin: 0;
  padding: 13px 8px 8px 5px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.page {
  padding-bottom: 30px;
}
.page p {
  letter-spacing: .2px;
  line-height: 20px;
  margin: 4px 0 12px;
  color: rgba(0,0,0,0.87);
  font-family: Roboto,arial,sans-serif;
  font-size: 14px;
  font-weight: 400;
}
.page h2 {
  color: #3c4043;
  font-family: 'Google Sans',sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  margin: 0;
  padding: 32px 0 8px 0;
}
.page h3 {
  color: #3c4043;
  font-family: 'Google Sans',sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 28px;
  margin: 24px 0 8px;
}
code {
  color: #060;
  font-family: monospace;
}
strong {
  font-weight: 500;
}
.form .field-block {
  border-bottom: 1px solid #eee;
  color: #222;
  min-width: 400px;
  padding-top: 18px;
  padding-bottom: 10px;
  padding-right: 20px;
  position: relative;
}
.form h3 {
  display: block;
  margin-block-start: 0.83em;
  margin-block-end: 0.83em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-size: 100%;
  font-weight: bold;
  font-family: arial,sans-serif;
  color: #222;
}
.form input {
  width: 280px;
}
.form .form-buttons {
  display: block;
  font-family: arial,sans-serif;
  padding-top: 18px;
  text-align: right;
}
.form .form-buttons input[type="submit"] {
    background-color: #f2f2f2;
    color: #757575;
    font-weight: bold;
    height: 36px;
    width: 142px;
    border: 1px;
    border-radius: 2px;
    margin: 11px 4px;
    text-align: center;
}

.form .form-buttons input[type="submit"]:hover {
    border: 1px solid #c6c6c6;
}

.form .form-buttons input[type="submit"]:focus {
    border: 1px solid #4d90fe;
}
</style>



